   .error-page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f0f0;
  }
  
  .error-page-heading {
    font-size: 3rem;
    margin-bottom: 1rem;
    color: #555;
  }
  
  .error-page-message {
    font-size: 1.5rem;
    margin-bottom: 2rem;
    color: #888;
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
  }
  
  .error-page-tip {
    font-size: 1rem;
    color: #777;
  }
  
  @keyframes shake {
    10%, 90% {
      transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(4px, 0, 0);
    }
  }
  